import './App.css';
import './components/game.js';
let games = require('./games.json');

function App() {
  const gamesDisplay = games.map((game, index) => {
    <li key={index}>
      <game game={game}></game>
    </li>
  });

  return (
    <div className="App">
      <header className="App-header">
        <h1>S.I.B.M.</h1>
        <h4>For Super Import Business Meeting use only</h4>
        <p>
          <ul>
            <div>{gamesDisplay}</div>
          </ul>
        </p>
      </header>
    </div>
  );
}

export default App;
